<template>
  <div class="system">
    <el-button type="primary" @click="SHOW_CAR_TYPE_OPTIONS = true">车型设置</el-button>
    <el-button type="primary" @click="SHOW_CAR_COLOR_OPTIONS = true">车色设置</el-button>

    <el-dialog
      title="车型设置"
      :visible.sync="SHOW_CAR_TYPE_OPTIONS"
      v-if="SHOW_CAR_TYPE_OPTIONS"
      :close-on-click-modal="false">
      <car-type-options ref="carTypeOptionsDialog"></car-type-options>
      <span slot="footer" class="dialog-footer">
        <el-button @click="SHOW_CAR_TYPE_OPTIONS = false">取 消</el-button>
        <el-button @click="handleCarTypeOptionsSubmit" type="primary" :loading="LOADING_CAR_TYPE_OPTIONS">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="车色设置"
      :visible.sync="SHOW_CAR_COLOR_OPTIONS"
      v-if="SHOW_CAR_COLOR_OPTIONS"
      :close-on-click-modal="false">
      <car-color-options ref="carColorOptionsDialog"></car-color-options>
      <span slot="footer" class="dialog-footer">
        <el-button @click="SHOW_CAR_COLOR_OPTIONS = false">取 消</el-button>
        <el-button @click="handleCarColorSubmit" type="primary" :loading="LOADING_CAR_COLOR_OPTIONS">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import $axios from '@/plugins/ajax';
import CarTypeOptions from './dialog/CarTypeOptions';
import CarColorOptions from './dialog/CarColorOptions';

export default {
  components: {
    CarTypeOptions,
    CarColorOptions
  },

  data() {
    return {
      SHOW_CAR_TYPE_OPTIONS: false,
      LOADING_CAR_TYPE_OPTIONS: false,

      SHOW_CAR_COLOR_OPTIONS: false,
      LOADING_CAR_COLOR_OPTIONS: false
    };
  },

  methods: {
    handleCarTypeOptionsSubmit() {
      this.LOADING_CAR_TYPE_OPTIONS = true;
      let tree = this.$refs['carTypeOptionsDialog'].tree;
      $axios.post('/setCarTypeOptions', tree).then((res) => {
        if (res.data.result) {
          this.$store.commit('SET_CAR_TYPE_OPTIONS', tree);
          this.$message.success(res.data.message);
          this.LOADING_CAR_TYPE_OPTIONS = false;
          this.SHOW_CAR_TYPE_OPTIONS = false;
        } else {
          this.$message.error(res.data.message);
          this.LOADING_CAR_TYPE_OPTIONS = false;
        }
      });
    },

    handleCarColorSubmit() {
      this.LOADING_CAR_COLOR_OPTIONS = true;
      let colors = this.$refs['carColorOptionsDialog'].list;
      $axios.post('/setCarColorOptions', colors).then((res) => {
        if (res.data.result) {
          this.$store.commit('SET_CAR_COLOR_OPTIONS', colors);
          this.$message.success(res.data.message);
          this.LOADING_CAR_COLOR_OPTIONS = false;
          this.SHOW_CAR_COLOR_OPTIONS = false;
        } else {
          this.$message.error(res.data.message);
          this.LOADING_CAR_COLOR_OPTIONS = false;
        }
      });
    }
  }
};
</script>
