<template>
  <div class="car-options">

    <el-button type="primary" @click="handleAddRoot">添加根车型</el-button>

    <el-tree :data="tree" :expand-on-click-node="false" draggable>
      <span slot-scope="{ node, data }">
        <el-input
          v-model="data.label"
          v-if="data.showInput"
          size="mini"
          :ref="data.label"
          @blur="handleBlur(data)"
          @keydown.enter.native="handleEnter(data)"></el-input>
        <span class="node-name" v-else>{{ node.label }}</span>
        <span class="actions" v-if="!data.showInput">
          <i class="el-icon-edit" @click="handleEdit(data)" title="修改车型"></i>
          <i class="el-icon-delete" @click="handleDelete(node, data)" title="删除车型"></i>
          <i class="el-icon-folder-add" @click="handleAppend(data)" title="新增子节点"></i>
        </span>
      </span>
    </el-tree>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tree: null
    };
  },

  computed: {
    carTypeOptions() {
      return this.$store.state.carTypeOptions;
    }
  },

  created() {
    this.tree = JSON.parse(JSON.stringify(this.carTypeOptions));
    this.fixData(this.tree);
  },

  methods: {
    handleAddRoot() {
      this.tree.push({ label: '新车型', showInput: false });
    },

    handleEdit(data) {
      data.showInput = true;
      this.$nextTick(() => {
        this.$refs[data.label].focus();
      });
    },

    handleBlur(data) {
      data.showInput = false;
    },

    handleEnter(data) {
      this.$refs[data.label].blur();
    },

    async handleDelete(node, data) {
      let operation = '';
      if (data.children) {
        try {
          operation = await this.$confirm('此操作将删除此车型及其所有子车型，是否继续？', '提示', {
            confirmButtonText: '确认删除',
            cancelButtonText: '取消',
            type: 'warning'
          });
        } catch (e) {
          operation = e;
        }
      }
      if (operation !== 'cancel') {
        const parent = node.parent;
        const children = parent.data.children || parent.data;
        const index = children.findIndex(d => d.label === data.label);
        children.splice(index, 1);
      }
    },

    handleAppend(data) {
      const newChild = { label: '新车型', showInput: false };
      if (!data.children) {
        this.$set(data, 'children', []);
      }
      data.children.push(newChild);
    },

    fixData(arr) {
      for (let i = 0; i < arr.length; i++) {
        this.$set(arr, i, Object.assign({}, arr[i], { showInput: false }));
        if (arr[i].children) {
          this.fixData(arr[i].children);
        }
      }
    }
  }
};
</script>

<style>
  .car-options .el-tree {
    margin-top: 10px;
  }
  .car-options .node-name {
    margin-right: 20px;
  }
  .car-options .el-tree-node__content {
    height: 28px;
  }
  .car-options .el-tree-node__content .actions {
    display: none;
  }
  .car-options .el-tree-node__content:hover .actions {
    display: inline;
  }
  .car-options .el-tree-node__content .actions i+i {
    margin-left: 10px;
  }
  .car-options .el-tree-node__content .actions i:hover {
    color: #20a0ff;
  }
</style>
