<template>
  <div class="car-color">
    <el-tag
      v-for="(item, index) in list"
      :key="index"
      :closable="closable"
      @close="handleClose(index)">{{item.label}}</el-tag>
    <el-input
      v-if="inputVisible"
      v-model="inputValue"
      class="input-new-tag"
      ref="saveTagInput"
      size="small"
      @keyup.enter.native="handleInputConfirm"
      @blur="handleInputConfirm">
    </el-input>
    <el-button
      v-else
      class="button-new-tag"
      size="small"
      @click="showInput"
      >+ 添加</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      inputVisible: false,
      inputValue: ''
    };
  },

  computed: {
    carColorOptions() {
      return this.$store.state.carColorOptions;
    },

    closable() {
      return this.list.length > 1;
    }
  },

  created() {
    this.list = JSON.parse(JSON.stringify(this.carColorOptions));
  },

  methods: {
    handleClose(index) {
      this.list.splice(index, 1);
    },

    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs['saveTagInput'].$refs['input'].focus();
      });
    },

    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.list.push({
          label: inputValue,
          color: '#fff'
        });
      }
      this.inputVisible = false;
      this.inputValue = '';
    }
  }
};
</script>

<style>
  .car-color .el-tag + .el-tag {
    margin-left: 10px;
  }
  .car-color .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .car-color .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
</style>
